import React, { useContext, useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { ApiContext } from '../ApiContext'


const Chart = (props) => {
    const [api] = useContext(ApiContext);
    const [chartData, setchartData] = useState({ labels: [] })

    useEffect(() => {
        let newLabels = []
        api.temps.map(datev => {
            return newLabels.push(datev.date_created.substring(0, 16))
        })
        let newTemps = []
        api.temps.map(tempv => {
            return newTemps.push(tempv.temperature)
        })
        let newHums = []
        api.temps.map(tempv => {
            return newHums.push(tempv.humidity)
        })

        setchartData({
            labels: newLabels, datasets: [
                {
                    label: 'Temperatures',
                    yAxisID: 'Temperatures',
                    data: newTemps,
                    pointStyle: 'line',
                    borderColor: ['rgba(232, 22, 22, 0.7  )'],
                    borderDash: [],
                    pointRadius: 0,
                    borderWidth: 3,
                    fill: false
                },
                {
                    label: 'Humidity',
                    yAxisID: 'Humidity',
                    data: newHums,
                    pointStyle: 'line',
                    borderColor: ['rgba(63, 191, 63, 0.7   )'],
                    pointRadius: 0,
                    borderWidth: 3,
                    fill: false
                }
            ]
        })

    }, [api.temps])

    if (!api.temps || !api.temps.length) { return <div>No data for Chart</div> }
    return (

        <div className="chart">
            <h3>{props.headline}</h3>
            <Line
                data={chartData}
                options={{
                    maintainAspectRatio: true,
                    legend: {
                        display: true,
                        position: 'top',
                        labels: {
                            fontColor: "#000080",
                        }
                    },
                    scales: {
                        yAxes: [{
                            id: 'Temperatures',
                            type: 'linear',
                            position: 'left',
                            ticks: {
                                callback: function (value, index, values) {
                                    return value + ' C째';
                                }
                            }
                        }, {
                            id: 'Humidity',
                            type: 'linear',
                            position: 'right',
                            ticks: {
                                max: 100,
                                min: 0,
                                callback: function (value, index, values) {
                                    return value + '%';
                                }
                            }
                        }]
                    }

                }
                }
            />
        </div>

    )
}

export default Chart;