import React, { useContext } from 'react';
import Nav from 'react-bootstrap/Nav';
import { ApiContext } from '../ApiContext'



const NavItem = (props) => {
    const [api, setApi] = useContext(ApiContext)

    function changePath() {
        setApi({
            ...api,
            apiEndPoint: props.endPoint,
            tempDescription: props.button_title
        })
    }

    return (
        <Nav.Link onClick={changePath}>{props.button_title}</Nav.Link>
    )
}
export default NavItem;