import React, { useContext } from 'react';
import { ApiContext } from './ApiContext';

import Row from 'react-bootstrap/Row';
import Chart from './components/Chart';
import Headline from './components/Headline'
import Navigation from './components/Navigation';
import InfoColumn from './components/InfoColumn';



function App(props) {
  const [api] = useContext(ApiContext);
  if (!api.temps || !api.temps.length) { return <div>No data for TemperatureCounter</div> }

  const numberOfTemps = api.temps.length
  const lastTemp = api.temps.slice(-1)[0];
  const currentTemp = { ...lastTemp }
  const allTemps = api.temps.reduce(function (prev, cur) {
    return prev + cur.temperature;
  }, 0);
  const allHumidity = api.temps.reduce(function (prev, cur) {
    return prev + cur.humidity;
  }, 0);
  const avgTemps = allTemps / numberOfTemps;
  const avgHumidity = allHumidity / numberOfTemps;

  const temps = api.temps.map(a => a.temperature);
  const hums = api.temps.map(a => a.humidity);


  return (
    <div className="App">
      <Navigation />
      <Headline />
      <Row>
        <InfoColumn name="Number of Measurements" data={numberOfTemps} />
      </Row>
      <Row>
        <InfoColumn name="Current Temperature" data={currentTemp.temperature + "째C"} />
        <InfoColumn name="Current Humidity" data={currentTemp.humidity + '%'} />
        <InfoColumn name="Average Temperature" data={Math.floor(avgTemps * 100) / 100 + '째C'} />

      </Row>
      <Row>
        <InfoColumn name="Average Humidity" data={Math.floor(avgHumidity * 100) / 100 + '%'} />
        <InfoColumn name="Max Temperature" data={Math.max(...temps) + '째C'} />
        <InfoColumn name="Max Humidity" data={Math.max(...hums) + '%'} />
      </Row>

      <Chart />
    </div>
  );
}

export default App;
