import React, { useRef, useState, useEffect, createContext } from 'react';

function useInterval(callback, delay) {
    const savedCallback = useRef();

    // Remember the latest callback.
    useEffect(() => {
        savedCallback.current = callback;
    }, [callback]);

    // Set up the interval.
    useEffect(() => {
        function tick() {
            savedCallback.current();
        }
        if (delay !== null) {
            let id = setInterval(tick, delay);
            return () => clearInterval(id);
        }
    }, [delay]);
}


export const ApiContext = createContext();

export const ApiProvider = (props) => {
    let [requestCount, setRequestCount] = useState(0);

    const [api, setApi] = useState({
        apiPath: 'https://th2e-backend.herokuapp.com/temperature/',
        apiEndPoint: 'last_seven_days',
        temps: [],
        tempDescription: 'Last seven days'
    });

    function fetchUrl() {

        fetch(api.apiPath + api.apiEndPoint).then(response =>
            response.json().then(data => {
                setApi({
                    ...api,
                    temps: data
                })
            }))
    }

    // Run every 5 minute
    const delay = 60000 * 5;

    useInterval(() => {
        fetchUrl()
        setRequestCount(requestCount + 1);
    }, delay);

    useEffect(() => {
        fetchUrl()
    }, [api.apiEndPoint]) // eslint-disable-line react-hooks/exhaustive-deps

    return (
        <ApiContext.Provider value={[api, setApi]}>
            {props.children}
        </ApiContext.Provider>
    )
}

